import { renderComponent } from "../react-dom";
export default class Component {
  constructor(props = {}) {
    this.props = props;
    this.state = {};
  }
  setState(stateChange) {
    // console.log("11111111");
    // Object.assign(this.state, stateChange);
    // renderComponent(this);
    enquenueSetState(stateChange, this);
  }
}

function defer(fn) {
  return Promise.resolve().then(fn);
}
/*
    1. å¼‚æ­¥æ›´æ–°state,çŸ­æ—¶é—´æŠŠå¤šä¸ªsetStateåˆå¹¶ä¸€ä¸ª
    2. ä¸€æ®µæ—¶é—´ä¹‹åï¼Œæ¸…ç©ºé˜Ÿåˆ—ï¼Œæ¸²æŸ“ç»„ä»¶
  */
const setStateQueue = [];
// ä¿å­˜å½“å‰çš„ç»„ä»¶
const renderQueue = [];
function enquenueSetState(stateChange, component) {
  // å»¶è¿Ÿè°ƒç”¨
  if (setStateQueue.length === 0) {
    // defer(flush)
    setTimeout(() => {
      flush();
    }, 0);
  }

  // 1åˆå¹¶å¤šä¸ªsetState
  setStateQueue.push({
    stateChange,
    component,
  });

  // å¦‚æœrenderQueueæ²¡æœ‰ç»„ä»¶ï¼Œæ·»åŠ è‡³é˜Ÿåˆ—ä¸­
  let r = renderQueue.some((item) => {
    return item === component;
  });
  if (!r) {
    // ç¬¬ä¸€æ¬¡æ·»åŠ 
    renderQueue.push(component);
  }
}
// ä¸€æ®µæ—¶é—´ä¹‹å
function flush() {
  let item, component;
  while ((item = setStateQueue.shift())) {
    const { stateChange, component } = item;

    // stateChangeå¯èƒ½æ˜¯æ•°å€¼ï¼Œå¯èƒ½æ˜¯å‡½æ•°
    // ä¿å­˜ä¹‹å‰çš„çŠ¶æ€
    if (!component.prevState) {
      component.prevState = Object.assign({}, component.state);
    }

    if (typeof stateChange === "function") {
      // æ˜¯ä¸€ä¸ªå‡½æ•°
      Object.assign(
        component.state,
        stateChange(component.prevState, component.props)
      );
    } else {
      // æ˜¯ä¸€ä¸ªå¯¹è±¡
      Object.assign(component.state, stateChange);
    }

    // prev
    component.prevState = component.state;
  }

  while ((component = renderQueue.shift())) {
    renderComponent(component);
  }
}
